* {
  margin: 0;
  padding: 0;
  -webkit-print-color-adjust: exact; }

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace; }

hr {
  margin: 0.25vh 0;
  border: none;
  background-color: black;
  color: black;
  height: 1px; }

h1 {
  font-size: 3.25vh; }

h2 {
  font-size: 2.75vh;
  font-style: italic;
  font-weight: 400; }

h3 {
  font-size: 2.5vh; }

h4 {
  font-size: 1.75vh; }

p {
  font-size: 1.5vh; }

ul {
  padding: 0.5vh 0 0 0; }

li {
  font-size: 1.25vh; }

a {
  position: relative;
  text-decoration: none;
  color: black; }
  a:before {
    background-color: black;
    content: "";
    height: 1px;
    position: absolute;
    bottom: -1px;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    -webkit-transition: width 0.3s ease-in-out;
    transition: width 0.3s ease-in-out;
    width: 100%; }
  a:hover {
    color: #0000ee; }
    a:hover:before {
      width: 0; }

.app-container {
  width: 100vw;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background: #464646; }

.page-container {
  width: 77.27273vh;
  height: 100vh;
  display: flex;
  box-shadow: 0px 1px 15px black; }

.main-container {
  width: 51.51515vh;
  height: 100vh;
  background: white; }

.sidebar-container {
  width: 25.75758vh;
  height: 100vh;
  background: #d7d7d7; }

.padding {
  padding: 1vh 2vh 0 2vh; }

.group {
  margin: 0.5vh 0 0 0; }

.header {
  display: flex;
  align-items: center;
  justify-content: space-between; }
  .header div {
    display: flex;
    align-items: center; }

.location {
  font-style: italic; }

.description {
  padding: 0 2vh; }

.skillset {
  margin: 1vh 0; }

.contact {
  display: flex;
  align-items: center;
  margin: 1vh 0 0 0; }
  .contact img {
    width: 1.75vh;
    margin: 0 0.75vh 0 0; }

.repo {
  display: flex;
  align-items: center;
  margin: 0 0 0 1vh; }
  .repo img {
    margin: 0 0 0 0.5vh; }
  .repo #star {
    width: 1.75vh; }
  .repo #fork {
    width: 1.25vh; }

@media screen and (max-width: 600px) {
  hr {
    margin: 0.32353vw 0; }
  h1 {
    font-size: 4.20588vw; }
  h2 {
    font-size: 3.55882vw; }
  h3 {
    font-size: 3.23529vw; }
  h4 {
    font-size: 2.26471vw; }
  p {
    font-size: 1.94118vw; }
  ul {
    padding: 0.64706vw 0 0 0; }
  li {
    font-size: 1.61765vw; }
  .page-container {
    width: 100vw;
    height: -webkit-max-content;
    height: -moz-max-content;
    height: max-content; }
  .main-container {
    width: 67vw;
    height: auto;
    padding-bottom: 2vw; }
  .sidebar-container {
    width: 33vw;
    height: auto;
    padding-bottom: 2vw; }
  .padding {
    padding: 1.29412vw 2.58824vw 0 2.58824vw; }
  .group {
    margin: 0.64706vw 0 0 0; }
  .description {
    padding: 0 2.58824vw; }
  .skillset {
    margin: 1.29412vw 0; }
  .contact {
    margin: 1.29412vw 0; }
    .contact img {
      width: 2.26471vw;
      margin: 0 0.97059vw 0 0; }
  .repo {
    margin: 0 0 0 1.29412vw; }
    .repo img {
      margin: 0 0 0 0.64706vw; }
    .repo #star {
      width: 2.26471vw; }
    .repo #fork {
      width: 1.61765vw; } }

@media print {
  .page-container {
    box-shadow: none; }
  a {
    color: black; }
    a:before {
      background-color: unset;
      content: unset;
      height: unset;
      position: unset;
      bottom: unset;
      left: unset;
      -webkit-transform: unset;
              transform: unset;
      -webkit-transition: unset;
      transition: unset;
      width: unset; } }

